import React from 'react';

import './../../../assets/scss/style.scss';
import Aux from "../../../hoc/_Aux";
import Breadcrumb from "../../../App/layout/AdminLayout/Breadcrumb";
import axios from 'axios'
import jwt_decode from "jwt-decode";

import config from '../../Config/config';

var urlbackend = require("../../../env/env")();
//import {settoken} from "../../../Store/StoreJwt"



class SignUp1 extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            username: "",
            password: ""
        };
    }
    onchangelogin(e) {

        this.setState({ username: e.target.value })
    }
    onchangepassword(e) {

        this.setState({ password: e.target.value })
    }
    async login() {
       // window.location.href= "/datta-able/react/default/dashboard/default"

        await   axios.post(urlbackend+"/signin",
            { username: this.state.username, password: this.state.password }).then( (response) => {
                console.log(jwt_decode(response.data.accessToken).roles)
                if(response.data.accessToken){
                   
                localStorage.setItem('token',response.data.accessToken)
                   // reactLocalStorage.setObject('token', response.data.accessToken);
                  //  window.location.href= "/datta-able/react/default/dashboard/default"
                
                  
                    
                     if(jwt_decode(response.data.accessToken).roles.includes("ROLE_ADMIN")){
                        this.props.history.push("/dashboard/default");
                      //  window.location.href= "/datta-able/react/default/dashboard/default"
                     }
                     if(jwt_decode(response.data.accessToken).roles.includes("ROLE_PROVIDER")){
                        this.props.history.push("/provider/dashboard/default");
                       // window.location.href= "/datta-able/react/default/provider/dashboard/default"
                     }
                     if(jwt_decode(response.data.accessToken).roles.includes("ROLE_DRIVER")){
                        this.props.history.push('/livreur/dashbord/getapk');

                     //   window.location.href= "/datta-able/react/default/v3/Listprovider"
                     }


                  }else{
                      alert("please check login")
              //  window.location.href= "/datta-able/react/default/auth/signin-1"

                  }

              

            }).catch((err) => {
                alert('check login')


            })

    }

    render() {
        return (
            <Aux>
                <Breadcrumb />
                <div className="auth-wrapper">
                    <div className="auth-content">
                        <div className="auth-bg">
                            <span className="r" />
                            <span className="r s" />
                            <span className="r s" />
                            <span className="r" />
                        </div>
                        <div className="card">
                            <div className="card-body text-center">
                                <div className="mb-4">
                                    <i className="feather icon-unlock auth-icon" />
                                <img src={config.images}  style={{width:"300px",height:"150px"}} alt ="pending"></img>
                                </div>

                                <div className="input-group mb-3">
                                    <input type="email" className="form-control" onChange={this.onchangelogin.bind(this)} value={this.state.username} placeholder="username" />
                                </div>
                                <div className="input-group mb-4">
                                    <input type="password" className="form-control" onChange={this.onchangepassword.bind(this)} value={this.state.password} placeholder="password" />
                                </div>
                                <div className="form-group text-left">
                                    <div className="checkbox checkbox-fill d-inline">
                                        <input type="checkbox" name="checkbox-fill-1" id="checkbox-fill-a1" />
                                      {/*   <label htmlFor="checkbox-fill-a1" className="cr"> Save credentials</label> */}
                                    </div>
                                </div>
                                <button onClick={() => this.login()} className="btn btn-primary shadow-2 mb-4">Login</button>
                          {/*       <p className="mb-2 text-muted">Forgot password? <NavLink to="/auth/reset-password-1">Reset</NavLink></p>
                                <p className="mb-0 text-muted">Don’t have an account? <NavLink to="/auth/signup-1">Signup</NavLink></p> */}
                            </div>
                        </div>
                    </div>
                </div>
            </Aux>
        );
    }
}

export default SignUp1;